import { ConfiguredConnectorTypes } from '../../types';
import { ActionInterface } from './action.interface';

export interface CreateConnectorInterface<T = object> {
  identifier: string;
  connectorType: ConfiguredConnectorTypes;
  tenantIdentifier: string;
  name: string;
  location: string;
  config: T;
  enabled: boolean;
}

export interface ConnectorInterface<T = object>
  extends CreateConnectorInterface<T> {
  createdAt: Date;
  updatedAt: Date;
  actions?: Array<ActionInterface>;
}
